import clsx from 'clsx'
import React, {FC, useEffect, useState, useRef} from 'react'
import {useIntl} from 'react-intl'
import {Link, withRouter, RouteComponentProps} from 'react-router-dom'
import {KTSVG, toAbsoluteUrl} from '../../../_metronic/helpers'
import moment from 'moment'
import {mainConfig} from '../../../config/mainConfig'
import {Skeleton, Row, Col, Select as Select1, Pagination} from 'antd'

import Helper from '../../utils/Helper'


let token = ''

const getWindowDimensions = () => {
  const {innerWidth: width, innerHeight: height} = window
  return {
    width,
    height,
  }
}

const SupplierGrnWrapper = (props: any) => {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions())

  
  const [loadingGrn, setloadingGrn] = useState<any>(false)
  const [loadingHeader, setloadingHeader] = useState<any>(false)



  const [grn, setgrn] = useState<any[]>([])

  const [prodHeader, setProdHeader] = useState<any>({})

  const [serched, setSearched] = useState<any[]>([])

  const [GRNPr, setPOPr] = useState<any>('Price')
  const [GRNQ, setPOQ] = useState<any>('Qty')
  const [supplierId, setSupplierId] = useState<any>('')

  const [columnName, setcolumnName] = useState<any>('Receipt_Date')
  const [pageNumber, setpageNumber] = useState<any>(1)
  const [sortOrder, setsortOrder] = useState<any>(0)
  const [rowCount, setrowCount] = useState<any>(0)
  const [GrnSortQty, setGrnSortQty] = useState<any>('Asc')
  const [ProductSortCode, setProductSortCode] = useState<any>('Asc')
  const [ProductSortDesc, setProductSortDesc] = useState<any>('Asc')
  const [PackSortSize, setPackSortSize] = useState<any>('Asc')
  const [SupplierSortCode, setSupplierSortCode] = useState<any>('Asc')
  const [GrnSortNo, setGrnSortNo] = useState<any>('Asc')
  const [GrnSortPrice, setGrnSortPrice] = useState<any>('Asc')
  const [ReceiptSortDate, setReceiptSortDate] = useState<any>('Asc')

  $('body').on('click', function (e) {
    var $currEl = $(e.currentTarget)

    if ($currEl.is('#kt_body') && serched.length > 0) {
      setSearched([])
    }
  })
/**
 * we have created this page for supplier grn full view.
 * get-supplier-grn/${temp}/${pageNumber}/${columnName}/${sortOrder} this api is called for the supplier grn 
 * we have added pagination which takes page number and column name and sort order for the supplier grn.
 */
  useEffect(() => {
    /**
     * this Effect run one time and it will check the props.location.state value which are comming from the supplier page
     * if the props.location.state value is undefined then it will take supplier id fron the local storage
     */
    if (props.location.state != undefined) {
      localStorage.setItem('supplierId', props.location.state.id)
    }

    let suppData = props.location.state

    let supplierData =
      props.location.state != undefined
        ? props.location.state.id
        : localStorage.getItem('supplierId')

    setSupplierId(supplierData)

    getData2(supplierData, columnName, pageNumber, sortOrder)

    function handleResize() {
      setWindowDimensions(getWindowDimensions())
    }

    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  const getData2 = (data: any, columnName: any, pageNumber: any, sortOrder: any) => {
     /**
     * This function runs when the effect run one time 
     */
    let temp = data
    setloadingGrn(true)

    setloadingHeader(true)

    try {
      fetch(
        `${mainConfig.MAIN_ENDPOINT}/api/get-supplier-grn/${temp}/${pageNumber}/${columnName}/${sortOrder}`,
        {
          method: 'get',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
        }
      ).then(function (response) {
        if (response.status != 200) {
          localStorage.clear()
          window.location.reload()
        }

        response.json().then(function (data) {
          setgrn(data.data.grn)
          setProdHeader(data.data.header)

          setSearched([])
          setrowCount(data.data.rowCount)
          setpageNumber(pageNumber)
          setloadingHeader(false)
          setloadingGrn(false)
        })
      })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  const onSelectSupplier = (e: any) => {
     /**
     * onSelectSupplier we get supplier grn details and store it in grn state object and use it on page render
     * @param e  the event object which gives supplier id we used this as api parameter.
     * 
     */
    let n = e
    setloadingGrn(true)

    setSearched([])

    const response = fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-supplier-grn/${n.id}`, {
      method: 'get',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    }).then(function (response) {
      if (response.status != 200) {
        return
      }

      response.json().then(function (data) {
        setgrn(data.data.grn)

        setProdHeader(data.data.header)
        setloadingHeader(false)
        setloadingGrn(false)
        setSearched([])
        e = ''
      })
    })
  }

  const onSortGrnTable = (data: any) => {
     /**
     * this function is used to sor grn table data
     * @param data this parameter gived sorting data like column name 
     */
    setcolumnName(data)
    sortOrder == 0 ? setsortOrder(1) : setsortOrder(0)

    switch (data) {
      case 'Grn_Qty':
        if (GrnSortQty == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortQty('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortQty('Asc')
        }
        break
      case 'Product_Code':
        if (ProductSortCode == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setProductSortCode('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setProductSortCode('Asc')
        }
        break
      case 'Product_Desc':
        if (ProductSortDesc == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setProductSortDesc('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setProductSortDesc('Asc')
        }
        break
      case 'Pack_Size':
        if (PackSortSize == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setPackSortSize('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setPackSortSize('Asc')
        }
        break
      case 'Supplier_Code':
        if (SupplierSortCode == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setSupplierSortCode('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setSupplierSortCode('Asc')
        }
        break
      case 'Grn_No':
        if (GrnSortNo == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortNo('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortNo('Asc')
        }
        break
      case 'Grn_Price':
        if (GrnSortPrice == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortPrice('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setGrnSortPrice('Asc')
        }
        break
      case 'Receipt_Date':
        if (ReceiptSortDate == 'Asc') {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setReceiptSortDate('Desc')
        } else {
          getData2(localStorage.getItem('supplierId'), data, pageNumber, sortOrder)
          setReceiptSortDate('Asc')
        }
        break
    }
  }
  const handlePagination = (page: any) => {
    let supplierData =
      props.location.state != undefined
        ? props.location.state.id
        : localStorage.getItem('supplierId')

    setSupplierId(supplierData)
    setTimeout(() => {
      getData2(supplierData, columnName, page, sortOrder)
    }, 1000)

    setpageNumber(page)
  }
     /**
     * Token always required to call Api this is bareer token.
     * This token is created while login we maintain this in localstorage on device 
     */
  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <React.Fragment>
      {windowDimensions.width > 850 ? (
        <div className='row gy-5 gx-xl-4'>
          <div className={`card`}>
            {serched.length > 0 ? (
              <div className={`card`} tabIndex={1}>
                <div className='table-responsive h-350px'>
                  {serched.map((j) => {
                    return (
                      <div className='align-items-center mb-2 p-2'>
                        <div className='flex-grow-1 '>
                          <button
                            type='button'
                            className='btn btn-md btn-light-primary'
                            onClick={() => onSelectSupplier(j)}
                          >
                            {j.name} &nbsp;&nbsp;&nbsp;&nbsp; {j.code}
                          </button>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </div>
            ) : null}
          </div>
        </div>
      ) : null}
      <div className='d-none d-sm-none d-md-block'>
        <Row style={{marginTop: '4%'}}>
          <Col span={20}>
            <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
              <Link to='/home' className='pe-1'>
                <li className='breadcrumb-item pe-1'>Home</li>
              </Link>
              / &nbsp;
              <Link to={{pathname: '/supplier', state: {id: supplierId}}}>
                <li className='breadcrumb-item pe-1'>Supplier</li>
              </Link>
              /&nbsp;<li className='breadcrumb-item pe-3'>GRN</li>
            </ol>
          </Col>

          <Col span={4}>
            <Link to={{pathname: '/supplier', state: {id: supplierId}}}>
              <button className='btn btn-sm btn-primary '>Back To Supplier</button>
            </Link>
          </Col>
        </Row>
      </div>
      <div className=' d-sm-block d-md-none'>
        <Row style={{marginTop: '4%'}}>
          <Col span={16}>
            <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
              <Link to='/home' className='pe-1'>
                <li className='breadcrumb-item pe-1'>Home</li>
              </Link>
              / &nbsp;
              <Link to={{pathname: '/supplier', state: {id: supplierId}}}>
                <li className='breadcrumb-item pe-1'>Supplier</li>
              </Link>
              /&nbsp;<li className='breadcrumb-item pe-3'>GRN</li>
            </ol>
          </Col>

          <Col span={8}>
            <Link to={{pathname: '/supplier', state: {id: supplierId}}}>
              <button className='btn btn-sm btn-primary '>Back To Supplier</button>
            </Link>
          </Col>
        </Row>
      </div>

      {windowDimensions.width > 800 && (
        <React.Fragment>
          <React.Fragment>
            <div className='toolbar' id='kt_toolbar'>
              <div id='kt_toolbar_container' className='container-fluid d-flex flex-stack'>
                <div className='d-flex align-items-center py-5 justify-content-space-between '>
                  <a className='btn btn-outline btn-outline-dashed m-1' style={{cursor: 'text'}}>
                    <span
                      className='card-label fs-2 mb-1 text-purple header-custom'
                      style={{color: 'rgb(138, 38, 80)'}}
                    >
                      {prodHeader.Supplier_Name}
                    </span>
                  </a>
                </div>

                <div className='d-flex align-items-center py-5 justify-content-space-between'></div>

                <div className='d-flex align-items-center py-5 justify-content-space-between'>
                  <a className='btn btn-outline btn-outline-dashed m-1' style={{cursor: 'text'}}>
                    <span
                      className='card-label fs-2 mb-1 text-purple header-custom'
                      style={{color: 'rgb(138, 38, 80)'}}
                    >
                      {prodHeader.Supplier_Code}
                    </span>
                  </a>
                </div>
              </div>
            </div>
          </React.Fragment>
        
        </React.Fragment>
      ) }

      <div className='row gy-5 g-xl-10' style={{marginTop: '20px'}}></div>

      {loadingGrn == true ? (
        <Skeleton loading={loadingGrn} active></Skeleton>
      ) : (
        <div className='row g-5 gx-xl-8'>
          <div className='col-xl-12'>
            <div className='card-xxl-stretch mb-4 mb-xxl-7'>
              <div className='row gy-5 g-xl-4'>
                <div className={`card`}>
                  <div className='card-header border-0 pt-2'>
                    <h3 className='card-title align-items-start flex-column'>
                      <span
                        className='card-label fw-bolder fs-3 mb-1'
                        style={{color: 'rgb(138, 38, 80)'}}
                      >
                        GRN
                      </span>
                    </h3>
                    <div className='card-toolbar'></div>
                  </div>

                  <div className='card-body py-3 table-align-left'>
                    <div className='table-responsive h-600px table-scroll'>
                      <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                        <thead>
                          <tr className='fw-bolder text-muted'>
                            <th
                              className='min-w-100px cursor-pointer'
                              onClick={() => onSortGrnTable('Product_Code')}
                            >
                              Product Code
                              {ProductSortCode != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                            <th
                              className='min-w-100px cursor-pointer'
                              style={{textAlign: 'left'}}
                              onClick={() => onSortGrnTable('Product_Desc')}
                            >
                              Product Description
                              {ProductSortDesc != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                            <th
                              className='min-w-100px cursor-pointer'
                              onClick={() => onSortGrnTable('Pack_Size')}
                            >
                              Pack Size
                              {PackSortSize != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>

                            <th
                              className='min-w-100px cursor-pointer'
                              onClick={() => onSortGrnTable('Grn_No')}
                            >
                              GRN No
                              {GrnSortNo != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                            <th className='min-w-70px' onClick={() => onSortGrnTable('Grn_Price')}>
                              GRN Price
                              {GrnSortPrice != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                            <th className='min-w-70px ' onClick={() => onSortGrnTable('Grn_Qty')}>
                              GRN Qty
                              {GrnSortQty != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                            <th
                              className='min-w-100px cursor-pointer '
                              onClick={() => {
                                onSortGrnTable('Receipt_Date')
                              }}
                            >
                              Reciept Date
                              {ReceiptSortDate != 'Desc' ? (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr062.svg'}
                                />
                              ) : (
                                <KTSVG
                                  className='text-hover-primary'
                                  path={'/media/icons/duotune/arrows/arr068.svg'}
                                />
                              )}
                            </th>
                          </tr>
                        </thead>

                        <tbody>
                          {grn != undefined &&
                            grn.map((j: any) => {
                              return (
                                <tr>
                                  <td>
                                    <Link
                                      to={{
                                        pathname: '/product',
                                        state: {prod_id: j.Product_Id_Main},
                                      }}
                                    >
                                      <span className='text-danger fw-bolder text-hover-primary d-block fs-6 text-decoration-underline link'>
                                        {j.Product_Code}
                                      </span>
                                    </Link>
                                  </td>
                                  <td>
                                    <span
                                      className='text-dark fw-bolder text-hover-primary d-block fs-6'
                                      style={{textAlign: 'left'}}
                                    >
                                      {j.Product_Desc}
                                    </span>
                                  </td>
                                  <td>
                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                      {j.Pack_Size}
                                    </span>
                                  </td>
                                  <td>
                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                      {j.Grn_No}
                                    </span>
                                  </td>
                                  <td>
                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                      {Helper.addZero(j.price)}
                                    </span>
                                  </td>

                                  <td>
                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                      {j.Grn_Qty}
                                    </span>
                                  </td>

                                  <td>
                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                      {moment(j.Receipt_Date).format('DD-MMM-YYYY')}
                                    </span>
                                  </td>
                                </tr>
                              )
                            })}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <Pagination
                defaultCurrent={pageNumber}
                defaultPageSize={30}
                size='small'
                total={rowCount}
                onChange={handlePagination}
                style={{margin: '2%'}}
                showQuickJumper
                hideOnSinglePage={true}
                showSizeChanger={false}
              />
               
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  )
}
export default withRouter(SupplierGrnWrapper)
